import { graphql, useStaticQuery } from "gatsby";

export default () => {
  const {
    site: { siteMetadata },
  } = useStaticQuery(
    graphql`
      query SITE_METADATA_QUERY {
        site {
          siteMetadata {
            title
            description
            authorFirstName
            authorAddress
            authorEmail
            avatar
            social {
              twitter
              github
              telegram
            }
          }
        }
      }
    `
  );
  return siteMetadata;
};
