import React from "react";
import { navigate } from "gatsby"; //import navigate from gatsby
import styled from "styled-components";
import media from "styled-media-query";
import getSiteMetadata from "hooks/getSiteMetadata";

import { AnimatedUnderlineText } from "components/Text";

const IndexWrapper = styled.div`
  margin-left: auto;
  margin-right: auto;
  padding-top: 150px;
`;

const StyledH1 = styled.h1`
  font-size: 25px;
  font-weight: 700;
  margin-bottom: 3.2rem;
  text-align: left;
  ${media.greaterThan("small")`
    font-size: 35px;
    font-weight: 700;
  `}
  ${media.greaterThan("medium")`
    font-size: 56px;
    font-weight: 700;
  `}
`;

const Index = ({}) => {
  const { authorFirstName } = getSiteMetadata();

  return (
    <IndexWrapper>
      <StyledH1>
        Hi, my name is {authorFirstName}.
        <br />
        I&apos;m a programmer.
      </StyledH1>
      <AnimatedUnderlineText
        onClick={() => {
          navigate("/about");
        }}
      >
        Know more about me ...
      </AnimatedUnderlineText>
    </IndexWrapper>
  );
};

export default Index;
